import { MdArrowBackIosNew } from "react-icons/md";
import { useNavigate } from "react-router-dom";
export default function GoBack() {
  const navigate = useNavigate();
  const handleClick = () => {
    navigate(-1);
  };
  return (
    <div
      onClick={handleClick}
      className="w-8 h-8 flex justify-center items-center bg-gray-100 rounded-full"
    >
      <MdArrowBackIosNew />
    </div>
  );
}
