import { Link } from "react-router-dom";
import GoBack from "../assets/GoBack";
import LogoAsset from "../assets/LogoAsset";
import QrAsset from "../assets/QrAsset";

export default function Layout({ children, canGoBack, title, hiddenQR }) {
  return (
    <div className="w-full flex justify-center">
      <div className="w-full h-screen relative max-w-sm">
        <div className="w-full h-20 border-b border-neutral-300/50 grid grid-cols-5 items-center ">
          <div className="w-full h-full flex items-center text-gray-400">
            {canGoBack && <GoBack />}
          </div>
          <div className=" col-span-3 w-full flex justify-center">
            {canGoBack ? (
              <span className="text-xl text-gray-700 font-semibold">
                {title}
              </span>
            ) : (
              <LogoAsset />
            )}
          </div>
          <div></div>
        </div>
        {!hiddenQR && (
          <Link to="/qrpage">
            <div className="absolute  w-20 h-20 bg-neutral-300/20 bottom-10 right-4 rounded-full flex justify-center items-center shadow-sm">
              <QrAsset />
            </div>
          </Link>
        )}
        {children}
      </div>
    </div>
  );
}
