import { createBrowserRouter } from "react-router-dom";
import OutletComponent from "./components/OutletComponent";
import NotFound from "./app/NotFound";
import Page from "./Page";
import KakaoChannel from "./app/KakaoChannel";
import MissionPhotoShoot from "./app/MissionPhotoShoot";
import MissionPersonalColor from "./app/MissionPersonalColor";
import MissionInterviewVR from "./app/MissionInterviewVR";
import QRPage from "./app/QRPage";

const router = createBrowserRouter([
  {
    path: "/",
    element: <OutletComponent />,
    errorElement: <NotFound />,
    children: [
      {
        path: "",
        element: <Page />,
      },
      {
        path: "/mission1",
        element: <KakaoChannel />,
      },
      {
        path: "/mission2",
        element: <MissionPhotoShoot />,
      },
      {
        path: "/mission3",
        element: <MissionPersonalColor />,
      },
      {
        path: "/mission4",
        element: <MissionInterviewVR />,
      },
      {
        path: "/qrpage",
        element: <QRPage />,
      },
    ],
  },
]);

export default router;
